import * as React from 'react'
import { useEffect, useState } from 'react'

import { Position } from '../../../types'
import { syncFlush } from '../../../utils'
import { ClassicScheme } from '../types'

export type ConnectionContextValue = { start: Position | null, end: Position | null, path: null | string }

type PositionWatcher = (cb: (value: Position) => void) => (() => void)

type Props<Schemes extends ClassicScheme> = {
    Component: (props: {
        data: ClassicScheme['Connection'] & { isLoop?: boolean },
        start: Position | null,
        end: Position | null,
        path: string | null,
        styles?: () => any
    }) => React.JSX.Element | null,
    payload: Schemes['Connection']
    start: Position | PositionWatcher,
    end: Position | PositionWatcher,
    path(start: Position, end: Position): Promise<null | string>
}

export function ConnectionWrapper<Schemes extends ClassicScheme>(props: Props<Schemes>) {
  const [computedStart, setStart] = useState<Position | null>(null)
  const [computedEnd, setEnd] = useState<Position| null>(null)
  const [path, setPath] = useState<string | null>(null)
  const start = 'x' in props.start ? props.start : computedStart
  const end = 'x' in props.end ? props.end : computedEnd
  const flush = syncFlush()
  const Component = props.Component

  useEffect(() => {
    const unwatch1 = typeof props.start === 'function' && props.start(s => flush.apply(() => setStart(s)))
    const unwatch2 = typeof props.end === 'function' && props.end(s => flush.apply(() => setEnd(s)))

    return () => {
      unwatch1 && unwatch1()
      unwatch2 && unwatch2()
    }
  }, [])
  useEffect(() => {
    if (start && end) props.path(start, end).then(p => flush.apply(() => setPath(p)))
  }, [start, end])

  return (
    <Component data={props.payload} start={computedStart} end={computedEnd} path={path} />
  )
}
